'use client'

import { PlusIcon, SquarePenIcon, XIcon } from 'lucide-react';
import React, { useState } from 'react';
import AddressModal from './AddressModal';
import { useSelector } from 'react-redux';
import toast from 'react-hot-toast';
import { useRouter } from 'next/navigation';

const OrderSummary = ({ totalPrice, items }) => {
  const currency = process.env.NEXT_PUBLIC_CURRENCY_SYMBOL || '$';
  const router = useRouter();

  const addressList = useSelector(state => state.address.list);

  const [paymentMethod, setPaymentMethod] = useState('COD');
  const [selectedAddress, setSelectedAddress] = useState(null);
  const [showAddressModal, setShowAddressModal] = useState(false);
  const [couponCodeInput, setCouponCodeInput] = useState('');
  const [coupon, setCoupon] = useState('');

  const handleCouponCode = async (e) => {
    e.preventDefault();
  };

  const handlePlaceOrder = async (e) => {
    e.preventDefault();

    if (!selectedAddress) {
      toast.error('Please select an address');
      return;
    }

    /* ---------- STRIPE PAYMENT ---------- */
    if (paymentMethod === 'STRIPE') {
      const successUrl = `${window.location.origin}/orders?success=1`;
      const cancelUrl = window.location.href;

      const res = await fetch('/api/create-checkout-session', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          items,
          successUrl,
          cancelUrl,
        }),
      });

      if (!res.ok) {
        toast.error('Stripe checkout failed');
        return;
      }

      const data = await res.json();
      window.location.href = data.url;
      return;
    }

    /* ---------- COD ORDER ---------- */
    await fetch('/api/orders', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        items,
        total: totalPrice,
        address: selectedAddress,
        paymentMethod: 'COD',
      }),
    });

    toast.success('Order placed successfully');
    router.push('/orders');
  };

  return (
    <div className='w-full max-w-lg lg:max-w-[340px] bg-slate-50/30 border border-slate-200 text-slate-500 text-sm rounded-xl p-7'>
      <h2 className='text-xl font-medium text-slate-600'>Payment Summary</h2>

      <p className='text-slate-400 text-xs my-4'>Payment Method</p>

      <div className='flex gap-2 items-center'>
        <input
          type="radio"
          id="COD"
          checked={paymentMethod === 'COD'}
          onChange={() => setPaymentMethod('COD')}
        />
        <label htmlFor="COD">COD</label>
      </div>

      <div className='flex gap-2 items-center mt-1'>
        <input
          type="radio"
          id="STRIPE"
          checked={paymentMethod === 'STRIPE'}
          onChange={() => setPaymentMethod('STRIPE')}
        />
        <label htmlFor="STRIPE">Stripe Payment</label>
      </div>

      <div className='my-4 py-4 border-y border-slate-200'>
        <p>Address</p>

        {selectedAddress ? (
          <div className='flex gap-2 items-center'>
            <p>
              {selectedAddress.name}, {selectedAddress.city},{' '}
              {selectedAddress.state}, {selectedAddress.zip}
            </p>
            <SquarePenIcon
              size={18}
              className='cursor-pointer'
              onClick={() => setSelectedAddress(null)}
            />
          </div>
        ) : (
          <div>
            {addressList.length > 0 && (
              <select
                className='border p-2 w-full my-3 rounded'
                onChange={(e) =>
                  setSelectedAddress(addressList[e.target.value])
                }
              >
                <option value="">Select Address</option>
                {addressList.map((address, index) => (
                  <option key={index} value={index}>
                    {address.name}, {address.city}
                  </option>
                ))}
              </select>
            )}

            <button
              onClick={() => setShowAddressModal(true)}
              className='flex items-center gap-1 mt-1'
            >
              Add Address <PlusIcon size={18} />
            </button>
          </div>
        )}
      </div>

      <div className='flex justify-between py-4'>
        <p>Total:</p>
        <p className='font-medium'>
          {currency}
          {coupon
            ? (totalPrice - (coupon.discount / 100) * totalPrice).toFixed(2)
            : totalPrice.toLocaleString()}
        </p>
      </div>

      <button
        onClick={(e) =>
          toast.promise(handlePlaceOrder(e), {
            loading: 'Placing order...',
          })
        }
        className='w-full bg-slate-700 text-white py-2.5 rounded hover:bg-slate-900'
      >
        Place Order
      </button>

      {showAddressModal && (
        <AddressModal setShowAddressModal={setShowAddressModal} />
      )}
    </div>
  );
};

export default OrderSummary;
